export interface Dimensions {
  width: number;
  height: number;
}

export interface CropApi {
  fitToCropArea?: () => void;
  centerCropImage?: () => void;
}

export enum CropToolState {
  Pending = 0,
  Loading = 1,
  Ready = 2,
  Error = 3
}

export enum CropToolError {
  Source = 0,
  Size = 1,
  Load = 2,
  Crop = 3
}

export interface CropToolErrorEvent {
  errCode: CropToolError;
  errBody: string;
}
